














.welcome {
  max-width: 400px;
  margin-top: 100px;
}
.welcome h2{
  font-size: 3em;
}
.welcome button{
  margin: 30px auto;
}

.chat h2{
    font-size: 2.6em;
    margin-bottom: 40px;
}
.chat span{
    font-size: 1.4em;
}
.chat .time{
    display: block;
    font-size: 0.8em;
}
.message {
    max-height: 300px;
    overflow: auto;
}
.message::-webkit-scrollbar {
    width: 3px;
}
.message::-webkit-scrollbar-track {
    background: #ddd;
}
.message::-webkit-scrollbar-thumb {
    background: #aaa;
}
